import Vue from 'vue'
import Vuex from 'vuex'
import Api from "@/services/Api.js";
// import firebase from "firebase";


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: {},
    // token: firebase.auth().currentUser.uid,
    maintenanceMode: null,
    inventoryPageNumber: 1,
    inventoryPageCount: 1,
    inventoryPageSize: 10,
    inventoryTotalItems: 0,
    orderPageNumber: 1,
    orderPageCount: 1,
    orderPageSize: 10,
    totalOrders: 0,
    deliveryPageNumber: 1,
    deliveryPageCount: 1,
    deliveryPageSize: 10,
    totalDeliveries: 0,

    couponPageNumber: 1,
    couponPageCount: 1,
    couponPageSize: 10,
    totalCoupons: 0,

    notificationPageNumber: 1,
    notificationPageCount: 1,
    notificationPageSize: 10,
    totalNotifications: 0,

    userPageNumber: 1,
    userPageCount: 1,
    userPageSize: 10,
    totalUsers: 0,
  },
  getters: {
    getUser: state => {
      return state.user;
    },

    getmaintenanceMode: state => {
      return state.maintenanceMode;
    },

    getinventoryPageNumber: state => {
      return state.inventoryPageNumber;
    },

    getinventoryPageCount: state => {
      return state.inventoryPageCount;
    },

    getinventoryPageSize: state => {
      return state.inventoryPageSize;
    },

    getinventoryTotalItems: state => {
      return state.inventoryTotalItems;
    },

    getorderPageNumber: state => {
      return state.orderPageNumber;
    },

    getorderPageCount: state => {
      return state.orderPageCount;
    },

    getorderPageSize: state => {
      return state.orderPageSize;
    },

    gettotalOrders: state => {
      return state.totalOrders;
    },

    getdeliveryPageNumber: state => {
      return state.deliveryPageNumber;
    },

    getdeliveryPageCount: state => {
      return state.deliveryPageCount;
    },

    getdeliveryPageSize: state => {
      return state.deliveryPageSize;
    },

    gettotalDeliveries: state => {
      return state.totalDeliveries;
    },

    getcouponPageNumber: state => {
      return state.couponPageNumber;
    },

    getcouponPageCount: state => {
      return state.couponPageCount;
    },

    getcouponPageSize: state => {
      return state.couponPageSize;
    },

    gettotalCoupons: state => {
      return state.totalCoupons;
    },

    getnotificationPageNumber: state => {
      return state.notificationPageNumber;
    },

    getnotificationPageCount: state => {
      return state.notificationPageCount;
    },

    getnotificationPageSize: state => {
      return state.notificationPageSize;
    },

    gettotalNotifications: state => {
      return state.totalNotifications;
    },

    getuserPageNumber: state => {
      return state.userPageNumber;
    },

    getuserPageCount: state => {
      return state.userPageCount;
    },

    getuserPageSize: state => {
      return state.userPageSize;
    },

    gettotalUsers: state => {
      return state.totalUsers;
    },

  },
  mutations: {
    setUser(state, user) {
      state.user = user;
      // Object.assign(state.user, user);
    },

    setMaintenanceMode(state, maintenanceMode) {
      state.maintenanceMode = maintenanceMode;
    },

    setinventoryPageNumber(state, pagenumber) {
      state.inventoryPageNumber = pagenumber
    },

    setinventoryPageCount(state, pagecount) {
      state.inventoryPageCount = pagecount
    },

    setinventoryTotalItems(state, totalItems) {
      state.inventoryTotalItems = totalItems
    },

    setinventoryPageSize(state, size) {
      state.inventoryPageSize = size
    },

    setorderPageNumber(state, pagenumber) {
      state.orderPageNumber = pagenumber
    },

    setorderPageCount(state, pagecount) {
      state.orderPageCount = pagecount
    },

    settotalOrders(state, totalItems) {
      state.totalOrders = totalItems
    },

    setorderPageSize(state, size) {
      state.orderPageSize = size
    },

    setdeliveryPageNumber(state, pagenumber) {
      state.deliveryPageNumber = pagenumber
    },

    setdeliveryPageCount(state, pagecount) {
      state.deliveryPageCount = pagecount
    },

    settotalDeliveries(state, totalItems) {
      state.totalDeliveries = totalItems
    },

    setdeliveryPageSize(state, size) {
      state.deliveryPageSize = size
    },

    setcouponPageNumber(state, pagenumber) {
      state.couponPageNumber = pagenumber
    },

    setcouponPageCount(state, pagecount) {
      state.couponPageCount = pagecount
    },

    settotalCoupons(state, totalItems) {
      state.totalCoupons = totalItems
    },

    setnotificationPageNumber(state, pagenumber) {
      state.notificationPageNumber = pagenumber
    },

    setnotificationPageCount(state, pagecount) {
      state.notificationPageCount = pagecount
    },

    settotalNotifications(state, totalItems) {
      state.totalNotifications = totalItems
    },

    setnotificationPageSize(state, size) {
      state.notificationPageSize = size
    },

    setuserPageSize(state, size) {
      state.userPageSize = size
    },

    setuserPageNumber(state, pagenumber) {
      state.userPageNumber = pagenumber
    },

    setuserPageCount(state, pagecount) {
      state.userPageCount = pagecount
    },

    settotalUsers(state, totalUsers) {
      state.totalUsers = totalUsers
    },
  },
  actions: {
    setUser(context, user) {
      return new Promise((resolve, reject) => {
        let cUser = null;
        Api()
          .get("user", {
            params: {
              uuid: user.uid,
              permissions: 'fetch',
              collaboration: 'fetch'
            }
          })
          .then(res => {
            cUser = res.data;
            if (cUser.permissions.webappAcess != 16) {
              //webuploader details


              if (cUser.designation == "webappUploader" || cUser.designation == "admin") {
                Api()
                  .get('user/uploader', {
                    params: {
                      userId: user.uid,
                      status: 'active'
                    }
                  })
                  .then(uploader => {
                    if (uploader.data.length > 0) {
                      uploader.data.forEach(doc => {
                        cUser['totalUploads'] = doc.totalUploads;
                        cUser['package'] = doc.package;
                        cUser['status'] = doc.status;
                        cUser['amountPaid'] = doc.amountPaid;
                        context.commit('setUser', cUser);
                        resolve(cUser);
                      });
                    }

                  })
                  .catch(err => {
                    console.log(err.message);
                  });
              } else {
                // if uploader isn't there
                context.commit('setUser', cUser);
                resolve(cUser);
              }
            }
          })
          .catch(err => {
            reject(err);
          });
      });
    },

    patchMaintenanceMode(context) {
      Api()
        .get("aboutUs", {
          params: {
            name: 'underMaintenance'
          }
        })
        .then(res => {

          let value = (res.data.content === 'true');
          context.commit('setMaintenanceMode', value)
        })
        .catch(err => {
          console.log(err);
        });
    },

    patchinventoryPageNumber(context, pagenumber) {
      context.commit('setinventoryPageNumber', pagenumber)
    },

    patchinventoryPageCount(context, pagecount) {
      context.commit('setinventoryPageCount', pagecount)
    },

    patchinventoryTotalItems(context, totalItem) {
      context.commit('setinventoryTotalItems', totalItem)
    },

    patchinventoryPageSize(context, size) {
      context.commit('setinventoryPageSize', size)
    },

    patchorderPageNumber(context, pagenumber) {
      context.commit('setorderPageNumber', pagenumber)
    },

    patchorderPageCount(context, pagecount) {
      context.commit('setorderPageCount', pagecount)
    },

    patchtotalOrders(context, totalItem) {
      context.commit('settotalOrders', totalItem)
    },

    patchorderPageSize(context, size) {
      context.commit('setorderPageSize', size)
    },

    patchdeliveryPageNumber(context, pagenumber) {
      context.commit('setdeliveryPageNumber', pagenumber)
    },

    patchdeliveryPageCount(context, pagecount) {
      context.commit('setdeliveryPageCount', pagecount)
    },

    patchtotalDeliveries(context, totalItem) {
      context.commit('settotalDeliveries', totalItem)
    },

    patchdeliveryPageSize(context, size) {
      context.commit('setdeliveryPageSize', size)
    },

    patchcouponPageNumber(context, pagenumber) {
      context.commit('setcouponPageNumber', pagenumber)
    },

    patchcouponPageCount(context, pagecount) {
      context.commit('setcouponPageCount', pagecount)
    },

    patchtotalCoupons(context, totalItem) {
      context.commit('settotalCoupons', totalItem)
    },

    patchcouponPageSize(context, size) {
      context.commit('setcouponPageSize', size)
    },

    patchuserPageNumber(context, pagenumber) {
      context.commit('setuserPageNumber', pagenumber)
    },

    patchuserPageCount(context, pagecount) {
      context.commit('setuserPageCount', pagecount)
    },

    patchtotalUsers(context, totalItem) {
      context.commit('settotalUsers', totalItem)
    },

    patchuserPageSize(context, size) {
      context.commit('setuserPageSize', size)
    },

    patchnotificationPageNumber(context, pagenumber) {
      context.commit('setnotificationPageNumber', pagenumber)
    },

    patchnotificationPageCount(context, pagecount) {
      context.commit('setnotificationPageCount', pagecount)
    },

    patchtotalNotifications(context, totalItem) {
      context.commit('settotalNotifications', totalItem)
    },

    patchnotificationPageSize(context, size) {
      context.commit('setnotificationPageSize', size)
    },
  }
})



